    monospace;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
* {
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: right;
  height: 100vh;
}
.description {
  margin: 5px 0;
  font-family: roboto, sans-serif;
  font-size: 20px;
  position: relative;
  display: inline-block;
  -webkit-animation-name: slideTop;
          animation-name: slideTop;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  bottom: -1000px;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  opacity: 0;
  transition-property: opacity;
}
.description::after,
.description::before {
  content: "";
  position: absolute;
  height: 100%;
  /* top: 0;
  left: 0;
  bottom: 0;
  right: 0; */
  display: block;
}
.description::before {
  background-color: rgb(17, 17, 17);
  width: 100vw;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: -2;
}
.description::after {
  width: 100%;
  top: 0;
  background-color: whitesmoke;
  z-index: -1;
  outline: 5px solid whitesmoke;
}

@-webkit-keyframes slideTop {
  0% {
    bottom: -1000px;
    opacity: 0.5;
  }
  25% {
    bottom: -700px;
    opacity: 0.5;
  }
  0% {
    bottom: -10px;
    opacity: 0.5;
  }
  100% {
    bottom: 0px;
    opacity: 1;
  }
}

@keyframes slideTop {
  0% {
    bottom: -1000px;
    opacity: 0.5;
  }
  25% {
    bottom: -700px;
    opacity: 0.5;
  }
  0% {
    bottom: -10px;
    opacity: 0.5;
  }
  100% {
    bottom: 0px;
    opacity: 1;
  }
}

*,
